.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.nav-item {
  text-decoration: none; /*Убираем стандартное подчёркивание ссылки если оно ещё не убрано*/
  display: inline-block;
  /*делаем чтобы наша ссылка из строчного элемента превратилась в строчно-блочный
 line-height: 1; /*Задаём высоту строки (можно в пикселях)*/
  color: #ce1111; /*Задаём цвет ссылки*/
  margin-left: 30px;
}

.nav-item:after {
  display: block; /*превращаем его в блочный элемент*/
  content: ""; /*контента в данном блоке не будет поэтому в кавычках ничего не ставим*/
  height: 1.5px; /*задаём высоту линии*/
  width: 0%; /*задаём начальную ширину элемента (линии)*/
  background-color: #225384; /*цвет фона элемента*/
  transition: width 0.4s ease-in-out; /*данное свойство отвечает за плавное изменение ширины. Здесь можно задать время анимации в секундах (в данном случае задано 0.4 секунды)*/
}

.nav-item:hover:after,
.nav-item:focus:after {
  width: 100%;
}

.nav-item:hover {
  color: #225384;
}

.navbar-brand {
  font-family: 'Nerko One';
  font-size: 30px;
  text-decoration:none;

}
.navbar-brand:hover{
  animation: bounce 1s;
}
.noNavLink{
  text-decoration:none;
  color: black;
}
.noNavLink:hover{
  color: #565454;
}
@media (max-width: 978px) {
  .nav-item:focus:after{
    width: 50%;
  }
}
@media screen and (max-width: 984px) {
  .nav-item{width: 20%;}
}
@keyframes bounce {
  0%, 10%, 60%, 100% {
    transform: translateY(0);
    transform: translateY(0);
  }

  40% {
    transform: translateY(-10px);
    transform: translateY(-10px);
  }

  80% {
    transform: translateY(-5px);
    transform: translateY(-5px);
  }
}


@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
