.services {
    background: url('../../pictr/bisness3.png') rgba(0, 0, 0, 0.7) no-repeat center top fixed;
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover;
    margin: 0;
    max-height: 70vh;
    background-blend-mode: multiply;


}

@media screen and (max-width: 400px) {
    .services {
        max-height: 40vh;
        background: url('../../pictr/bisness4.png') rgba(0, 0, 0, 0.7) no-repeat center border-box;

    }
}

.h1ServicesPAge {


    padding-top: 150px;
    color: white;
    padding-bottom: 150px;
    margin-left: 100px;
    font: 400 70px/1.2 'Roboto Condensed', serif;
    font-family: 'Nerko One';
    line-height: 1.3em

}

@media screen and (max-width: 489px) {
.h1ServicesPAge {margin-left: 50px;}
}
@media screen and (max-width: 385px) {
    .h1ServicesPAge {margin-left: -20px;padding: 60px}
}